var axios = require("axios").default;

export const GetPictureUrl = (animal) => {
  return axios.get(
    "https://contextualwebsearch-websearch-v1.p.rapidapi.com/api/Search/ImageSearchAPI", 
    {
    params: {q: animal, pageNumber: '1', pageSize: '1', autoCorrect: 'true'}, 
    headers: { 
      'x-rapidapi-key': 'dcc3efa7f2mshfa2fefe0b7d48a3p18d48djsn743d3726ac82',
      'x-rapidapi-host': 'contextualwebsearch-websearch-v1.p.rapidapi.com'
  }});
};
