import logo from './logo.svg';
import './App.css';
import React, { useState, Component } from 'react';
import { GetPictureUrl }from './client';

class Main extends Component {
    constructor(props) {
      super(props);
      this.state = {
          weight: '',
          depression: '',
          animal: '',
          showBox: false,
          number: 0,
          image: ''
        };
      this.handleChange1 = this.handleChange1.bind(this);
      this.handleChange2 = this.handleChange2.bind(this);
      this.handleChange3 = this.handleChange3.bind(this);
      this.handleSubmit = this.handleSubmit.bind(this);
    }

    handleChange1(event) {
        this.setState({weight: event.target.value});
    }
    handleChange2(event) {
        this.setState({depression: event.target.value});
    }
    handleChange3(event) {
        this.setState({animal: event.target.value});
    }
    async GetPhoto (animal) {
      const {data} = await GetPictureUrl(animal);
      console.log(data.value[0].url);
      this.setState({image: data.value[0].url});
    }
    handleSubmit(event) {
        event.preventDefault();
        var num = (this.state.weight / this.state.animal.length-1) - (1.5 * this.state.depression);
        num = Math.floor(num);
        this.GetPhoto(this.state.animal);
        this.setState({
          showBox: true,
          number: num
        })
    }
  
    render() {
      return (
        <header className="App-header">
            <div>
                <form className="cForm" onSubmit={this.handleSubmit}>
                    <label className="cHeader" >Your Weight:</label><br/>
                    <input className="cInput" type="text" id="fname" name="fname" placeholder="Lbs."
                    value={this.state.weight} onChange={this.handleChange1}></input>
                    <label className="cHeader" >Your Depression:</label><br/>
                    <input className="cInput" type="text" id="fname" name="fname" placeholder="Out of 10"
                    value={this.state.depression} onChange={this.handleChange2}></input>
                    <label className="cHeader" >Animal to Fight:</label><br/>
                    <input className="cInput" type="text" id="fname" name="fname" placeholder="Singular"
                    value={this.state.animal} onChange={this.handleChange3}></input>
                    <input 
                      className="cSubmit" 
                      value={this.state.showBox && !this.state.img ? 'Calculating...' : 'Submit'}
                      type="submit"></input>
                </form>
            </div>
            {this.state.showBox && this.state.image && 
            <div className="showBox">
                <img className="cImage" src={this.state.image}></img>
                <p className="cP">You could probably fight {this.state.number} {this.state.animal}s...</p>
            </div>
            }
        </header>
      );
    }
  }
export default Main;
